import { 
  createUserWithEmailAndPassword,
  signInWithEmailAndPassword,
  updateProfile,
  sendPasswordResetEmail,
  EmailAuthProvider,
  reauthenticateWithCredential,
  updatePassword
} from 'firebase/auth';
import { 
  doc, 
  setDoc, 
  getDoc,
  serverTimestamp 
} from 'firebase/firestore';
import { auth, db } from './firebase';

class AuthService {
  async register(email, password, displayName) {
    try {
      const userCredential = await createUserWithEmailAndPassword(auth, email, password);
      const user = userCredential.user;

      await updateProfile(user, {
        displayName: displayName
      });

      await setDoc(doc(db, 'users', user.uid), {
        uid: user.uid,
        email: user.email,
        displayName: displayName,
        role: 'user',
        createdAt: serverTimestamp(),
        isActive: true
      });

      return {
        success: true,
        user: user
      };
    } catch (error) {
      console.error('Error en registro:', error);
      return {
        success: false,
        error: this.getErrorMessage(error.code)
      };
    }
  }

  async login(email, password) {
    try {
      const userCredential = await signInWithEmailAndPassword(auth, email, password);
      const user = userCredential.user;

      // REMOVIDO temporalmente: Verificaci√≥n de documento de usuario
      // Esta verificaci√≥n puede causar problemas si Firestore tiene latencia
      // El documento se crear√° cuando sea necesario en otro momento

      return {
        success: true,
        user: user
      };
    } catch (error) {
      return {
        success: false,
        error: this.getErrorMessage(error.code)
      };
    }
  }

  async resetPassword(email) {
    try {
      await sendPasswordResetEmail(auth, email);
      return {
        success: true,
        message: 'Se ha enviado un correo para restablecer la contrase√±a'
      };
    } catch (error) {
      console.error('Error al enviar correo de restablecimiento:', error);
      return {
        success: false,
        error: this.getErrorMessage(error.code)
      };
    }
  }

  async changePassword(currentPassword, newPassword) {
    try {
      const user = auth.currentUser;
      if (!user || !user.email) {
        throw new Error('No hay usuario autenticado');
      }

      const credential = EmailAuthProvider.credential(
        user.email,
        currentPassword
      );

      await reauthenticateWithCredential(user, credential);
      await updatePassword(user, newPassword);

      return {
        success: true,
        message: 'Contrase√±a actualizada exitosamente'
      };
    } catch (error) {
      console.error('Error al cambiar contrase√±a:', error);
      return {
        success: false,
        error: this.getErrorMessage(error.code)
      };
    }
  }

  async getUserData(uid) {
    try {
      const userDoc = await getDoc(doc(db, 'users', uid));
      if (userDoc.exists()) {
        return {
          success: true,
          data: userDoc.data()
        };
      } else {
        return {
          success: false,
          error: 'Usuario no encontrado'
        };
      }
    } catch (error) {
      console.error('Error al obtener datos del usuario:', error);
      return {
        success: false,
        error: 'Error al obtener datos del usuario'
      };
    }
  }

  getErrorMessage(errorCode) {
    const errorMessages = {
      'auth/email-already-in-use': 'Este correo electr√≥nico ya est√° registrado',
      'auth/invalid-email': 'El correo electr√≥nico no es v√°lido',
      'auth/operation-not-allowed': 'Operaci√≥n no permitida',
      'auth/weak-password': 'La contrase√±a debe tener al menos 6 caracteres',
      'auth/user-disabled': 'Esta cuenta ha sido deshabilitada',
      'auth/user-not-found': 'No existe una cuenta con este correo electr√≥nico',
      'auth/wrong-password': 'Contrase√±a incorrecta',
      'auth/invalid-credential': 'Credenciales inv√°lidas',
      'auth/too-many-requests': 'Demasiados intentos fallidos. Por favor, intente m√°s tarde',
      'auth/requires-recent-login': 'Por favor, vuelva a iniciar sesi√≥n para realizar esta operaci√≥n',
      'auth/network-request-failed': 'Error de conexi√≥n. Por favor, verifique su conexi√≥n a internet'
    };

    return errorMessages[errorCode] || 'Ha ocurrido un error. Por favor, intente nuevamente';
  }
}

export default new AuthService();